<template>
  <div id="myChart" ref="chart" style="width:100%;height:100%" v-resize="redraw"></div>
</template>

<script>
export default {
  name: 'hello',
  data () {
    return {
      myChart: {},
      datas: {}
    }
  },
  computed: {
    xAxisData: function () {
      let Arr = []
      for (let item of this.datas) {
        if (item.type === null || item.type === '' || item.type === undefined) {
          item.type = ''
        }
        Arr.push(item.type)
      }
      return Arr
    },
    count: function () {
      let Arr = []
      for (let item of this.datas) {
        Arr.push(item.count)
      }
      return Arr
    }
  },
  props: {
    /*  */
    data: {}
  },
  watch: {
    data () {
      this.dataHandle()
    }
  },
  methods: {
    /*  */
    dataHandle () {
      this.datas = this.data
      this.drawLine()
    },
    drawLine () {
      // domecharts
      this.myChart = this.$echarts.init(this.$refs.chart)
      // 
      this.myChart.setOption({
        title: {
          text: '',
          x: '15px',
          top: 10
        },
        grid: {
          left: '5%',
          right: '5%',
          bottom: '7%',
          top: '80px',
          containLabel: true
        },
        // legend: {
        //   data: ['', '', '', ''],
        //   top: 30,
        //   right: 60
        // },
        tooltip: {
          trigger: 'axis'
        },
        xAxis: {
          type: 'category',
          data: this.xAxisData,
          axisLabel: {
            interval: 0,
            formatter: (value, index) => {
              if (index % 2 !== 0) {
                return '\n' + value
              } else {
                return value
              }
            }
          }
        },
        yAxis: [
          {
            type: 'value',
            name: '/',
            minInterval: 1,
            min: 0,
            axisLabel: {
              formatter: '{value}'
            }
          }
        ],
        series: [
          {
            // name: '',
            type: 'bar',
            z: 3,
            itemStyle: {
              normal: {
                color: 'rgb(212,130,101)'
              }
            },
            data: this.count,
            barMaxWidth: 30
          }
        ]
      })
      // 
      window.addEventListener('resize', () => this.myChart.resize(), false)
    },
    redraw () {
      if (this.myChart.id !== undefined) {
        this.myChart.resize()
      }
    },
    // 64
    back64bit () {
      return this.myChart.getDataURL({
        type: 'jpeg',
        pixelRatio: 1,
        backgroundColor: '#fff'
      })
    }
  }
}
</script>

<style lang="scss" rel="stylesheet/scss" scoped>

</style>
